import {Component, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {DialogComponent} from './dialog/dialog.component';
import {Sheetmstdr2Service} from './sheetmstdr2.service';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {ConfirmationDialogComponent} from './confirmation-dialog/confirmation-dialog.component';
import { NotifierService } from 'src/app/notifier.service';
import { ReportService } from 'src/app/pages/generate-report/report.service';
import { ReportData } from 'src/app/data/report-data';
import { Location } from '@angular/common';

@Component({
  selector: 'app-sheetmstdr2',
  templateUrl: './sheetmstdr2.component.html',
  styleUrls: ['./sheetmstdr2.component.css']
})
export class Sheetmstdr2Component implements OnInit {

  dataSource: MatTableDataSource<any>;
  displayedColumns: string[] = ['id', 'state_code', 'state', 'up_to_2_yrs', 'schedule_2_to_5_yrs', 
  'schedule_5_to_10_yrs', 'over_10yrs', 'sub_total', 'over_draft_1', 'others', 'total_1', 'up_to_2_yrs_1', 
  'schedule_2_to_5_yrs_1', 'schedule_5_to_10_yrs_1', 'over_10yrs_1', 'sub_total_1','over_draft_2','others_1', 'total_2', 'up_to_2_yrs_2', 
  'schedule_2_to_5_yrs_2', 'schedule_5_to_10_yrs_2', 'over_10yrs_2', 'total_3' ];
  confirmDelete: boolean;
  reportData: ReportData;
  reportSelectedDate: string;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private dialog: MatDialog,
              private sheetmstdr2Service: Sheetmstdr2Service,
              private notifierService: NotifierService,
              protected readonly location: Location) { }

  ngOnInit(): void {
    this.getAllMSTDR2Data();
  }

  // tslint:disable-next-line:typedef

  openDialog(): void {
    this.dialog.open(DialogComponent, {
       width: '30%',
    }).afterClosed().subscribe(value => {
      if (value === 'save'){this.getAllMSTDR2Data();
      }
    });
  }

  goBack() {
    this.location.back();
  }

  getAllMSTDR2Data(): void {
    this.sheetmstdr2Service.getMSTDR2Data().subscribe({
      next: (res) => {
        console.log(res.sheet002);
        this.dataSource = new MatTableDataSource(res.sheet002);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }, error: () => {console.log('Error while fetching data');
      }
    });
  }

  // tslint:disable-next-line:typedef
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  editReportRow(row: any): void {
    this.dialog.open(DialogComponent, {width: '30%', data: row}).afterClosed().subscribe(value => {
      if (value === 'update'){
        this.getAllMSTDR2Data();
      }
    });
  }

  deleteReportRow(id: any): void {
    this.sheetmstdr2Service.deleteMSTDR2Data(id).subscribe({
      next: value => {
        this.notifierService.showNotification('Deleted successfully', 'OK', 'success');
        this.getAllMSTDR2Data();
      }, error: err => {
        this.notifierService.showNotification('Unable to delete', 'OK', 'error');
      }
    });
  }

  // tslint:disable-next-line:typedef
  openConfirmationDialog(id) {

    this.dialog.open(ConfirmationDialogComponent, {
      width: '17%',
      data: true
    }).afterClosed().subscribe(value => {
      if (value === 'delete'){
        this.deleteReportRow(id);
        this.getAllMSTDR2Data();
      }
    });
  }
}
